import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { VehicleRouteModel, VehicleStopModel } from 'src/app/shared/models/model';
import { ToastrService } from 'src/app/shared/services/toastr-message.service';
import { VechileRouteService } from 'src/app/shared/services/vechile-route.service';
import { VechileStopsService } from 'src/app/shared/services/vechile-stops.service';

@Component({
  selector: 'app-vehicle-stops',
  templateUrl: './vehicle-stops.component.html'
})
export class VehicleStopsComponent implements OnInit {

  display: boolean = false;
  vechileStopForm!: FormGroup;
  vechileRoutes: VehicleRouteModel[] = [];
  vechileStops: VehicleStopModel[] = [];
  routeId!: number;

  constructor(private fb: FormBuilder, private vechileRouteService: VechileRouteService, private vechileStopsService: VechileStopsService, private toastrService: ToastrService) {
    this.vechileStopForm = this.fb.group({
      id: [''],
      name: ['', Validators.required],
      routeId: ['', Validators.required],
    });
  }

  ngOnInit() {
    this.getVechileRoutes();
  }

  getVechileRoutes() {
    this.vechileRouteService.get().subscribe((response: any) => {
      this.vechileRoutes = response;

      if(this.vechileRoutes.length){
        this.routeId = this.vechileRoutes[0].id;
        this.getVechileStops();
      }
    });
  }

  getVechileStops() {
    this.vechileStopsService.get(this.routeId).subscribe((response: any) => {
      this.vechileStops = response;
    });
  }
  updateData() {
    let temp = [...this.vechileStops];
    temp = JSON.parse(JSON.stringify(temp));
    this.vechileStops = temp;
  }


  showDialog() {
    this.display = true;
    this.vechileStopForm.reset();
  }

  add() {
    const data = this.vechileStopForm.value;
    if (!data.id) {
      delete data.id;
    }

    this.vechileStopsService.create(data).subscribe({
      next: (response: any) => {
      if (!!response.id) {
        this.vechileStops.push(response);
        this.updateData();
          this.toastrService.success('Vechile Stop Create Successfully.');
        this.display = false;
      }
    },
    error: () => {
      this.toastrService.error('Vechile Stop Create Failed.');
    }
    });
  }

  edit() {
    this.vechileStopsService.update(this.vechileStopForm.value).subscribe((response: any) => {
      const data = this.vechileStopForm.value;
      const index = this.vechileStops.findIndex(i => i.id === data.id);
      this.vechileStops[index] = data;
      this.toastrService.success('Vechile Stop Updated Successfully.');
      this.display = false;
    });
  }


  onSubmit() {
    this.vechileStopForm.get('routeId')?.setValue(this.routeId);

    if (this.vechileStopForm.invalid) {
      return;
    }

    if (!this.vechileStopForm.value.id) {
      this.add();
    } else {
      this.edit();
    }
  }

  fillForm(vechileRoute: VehicleStopModel) {
    this.display = true;
    this.vechileStopForm.patchValue(vechileRoute);
  }

  delete(id: number) {
    this.vechileStopsService.delete(id).subscribe((response: any) => {
      const index = this.vechileStops.findIndex(i => i.id == id);
      this.vechileStops.splice(index, 1);
      this.toastrService.success('Vechile Stop Deleted Successfully.');
    });
  }

  public close(): void {
    window.alert('Are u Sure Leave From');
    this.display = false;
  }

}

