import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { VehicleStopModel } from '../models/model';

@Injectable({
  providedIn: 'root'
})
export class VechileStopsService {
  apiUrl = `${environment.apiUrl}/api/VechileStops`;

  constructor(private http: HttpClient) { }

  get(routeId: number) {
    return this.http.get(`${this.apiUrl}?routeId=${routeId}`);
  }

  getById(id: number) {
    return this.http.get(`${this.apiUrl}/${id}`);
  }

  create(vehicleStop: VehicleStopModel) {
    return this.http.post(this.apiUrl, vehicleStop);
  }

  update(vehicleStop: VehicleStopModel) {
    return this.http.put(`${this.apiUrl}/${vehicleStop.id}`, vehicleStop);
  }

  delete(id: number) {
    return this.http.delete(`${this.apiUrl}/${id}`);
  }
}
